import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { GoodSelectDemoComponent } from "app/showcase/custom-components/good-select-demo/good-select-demo.component";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { GoodSelectDemoRoutingModule } from "app/showcase/custom-components/good-select-demo/good-select-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { GoodSelectDemoBasicComponent } from "app/showcase/custom-components/good-select-demo/good-select-demo-basic.component";
import { GoodSelectDemoGoodIdComponent } from "app/showcase/custom-components/good-select-demo/good-select-demo-goodId.component";
import { GoodSelectModule } from "app/custom-components/custom-components.module";

@NgModule({
	declarations:[
		GoodSelectDemoComponent,
		GoodSelectDemoBasicComponent,
		GoodSelectDemoGoodIdComponent
	],
	imports: [
		GoodSelectDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		GoodSelectModule
	]
})
export class GoodSelectDemoComponentModule { }



